*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --main-color: #56E5CC;
  --secondary-color:#B0A4FF;
  --detail-color:#2F4858;
  --main-gradient:linear-gradient(to right top, #56e5cc, #00ddef, #00cfff, #69bcff, #b0a4ff);
}

.main{
  background-image: var(--main-gradient);
  min-height: 100vh;
  color: var(--detail-color);
  overflow-x: hidden;
}

li{
  list-style-type: none;
}

button{
  border: none;
  background-color: transparent;
}

hr{
  background-color: transparent;
}

/*#3E8A54 - #00776A - #05606C*/
/*Sections*/
.mainSection{
  width: 75%;
  min-width: 850px;
}
.secondarySection{
  width: 30%;
}
/*Posts*/
.postsContainer{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.post{
  width: 75%;
  margin: .5rem 0;
  border: none;
  overflow: hidden;
  border-radius: 10px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  user-select:none;
  padding: 0;
  border: 2px solid var(--detail-color);
}



.btn:hover{
  filter: contrast(125%);
  color: white;
}
.btnContainer{
  min-width: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--secondary-color);
  flex-direction: column;
}
.btnOptionsContainer{
  padding: 0;
  margin: 0;
  height: 90px;
}
.btnContainer button{
  font-size: 20px;
  color: var(--detail-color);
}
.card-body{
  max-width: calc(100% - 65px);
  color: var(--detail-color);
  font-style: none;
  text-decoration: none;
  cursor: pointer;
}
.card-body:hover{
  color: var(--detail-color);
  font-style: none;
  text-decoration: none;
}
.card-text{
  max-height: 150px;
  overflow-y: auto;
}
.btnContainer button:hover:not(.Give-a-coffe){
  transform: scale(1.2);
}
.btnOptionsContainer button:hover::after{
  position: absolute;
  font-size: 13px;
  bottom: 3px;
  right: 28px;
  font-weight: 600;
  color: var(--detail-color);
}
.delete:hover::after{
  content: "Delete";
}
.edit:hover::after{
  content: "Edit";
}
.save:hover::after{
  content: "Save";
}
.unsave:hover::after{
  content: "Unsave";
}
/*Cup Styles*/
.Give-a-coffe svg{
  transform: rotate(0deg);
  transition: .2s;
}
.Give-a-coffe:hover svg{
  transform: scale(1.2);
  transition: .2s;
}

.Give-a-coffe:active svg{
  transform: rotate(-45deg) scale(1.2);
  transition: .1s;
}

.Give-a-coffe:active .Give-a-coffeCounter{
  transform: scale(.8);
  transition: .2s;
}
.Give-a-coffeCounter{
  position: absolute;
  right: 38px;
  bottom: 4px;
  font-size: 18px;
  transition: .3s;
}

.coffeeCounter{
  font-size: 18px;
}

.coffeeCounter svg{
  transform: scale(.8);
}

.post .card-title{
  min-height: 30px;
  max-height: 100px;
  overflow-y: auto;
}

/*Post Form*/
.btn-main{
  background-image: var(--main-gradient);
  color: white;
}
.openForm, .closeForm{
  border-radius: 5px;
}
.openFormState:hover .openForm, .openFormState:hover .closeForm, .openForm:hover, .closeForm:hover{
  padding: 1px;
  transition: .3;
  background-color: var(--secondary-color);
  transition: .3s;
  border-radius: 5px;
}
.openFormState:hover .openForm::after, .openForm:hover::After{
  position: absolute;
  font-size: 12px;  
  bottom: 14px;
  right: 35px;
  font-weight: 600;
  color: var(--detail-color);
  content: "Open Form";
}
.openFormState:hover .closeForm::after,.closeForm:hover::after{
  position: absolute;
  font-size: 12px;  
  bottom: calc(100% - 29px);
  right: 35px;
  font-weight: 600;
  color: var(--detail-color);
  content: "Close Form";
}

/*navBar*/
.navBar{
  position: fixed;
  width: 100%;
  z-index: 100;
  gap: 10px;
  display: flex;
  padding: 15px;
  color: var(--detail-color);
  background-color: var(--detail-color);
}
.navText{
  display: flex;
  padding: 10px;
  border-radius: 10px 0 0 10px;
  align-items: center;
  user-select: none;
  background: var(--main-gradient);
  color: var(--detail-color);
  text-decoration: none;
}
.navText:hover{
  text-decoration: none;
  color: var(--detail-color);
}
.navSearch{
  display: flex;
  padding: 10px;
  border-radius: 0 10px 10px 0;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  background: var(--main-gradient);
  width: 55vw;
  min-width: 300px;
  max-width: 750px;
  transition: 2s;
}

.navSearch input{
  transition: 2s;
  background-color:var(--detail-color);
  border: none;
  color: white;
  width: 50vw;
  min-width: 200px;
  max-width: 675px;
  border-radius: 5px;
  padding: 0 10px;
}

.navSearch input:focus-visible{
  outline: none;
}

.navSearch svg{
  font-size: 25px;
}

.searchDisplay{
  position: absolute;
  background-color: var(--detail-color);
  color: white;
  padding: 10px;
  width: 55vw;
  min-width: 300px;
  max-width: 750px;
  margin: 0 -10px;
  top: 60px;
  z-index: 20;
  border: 5px solid var(--secondary-color);
  border-top: 0;
  border-radius: 0 0 10px 10px;
  user-select: none;
  max-height: 500px;
  overflow-x: auto;
}


.btn-search{
  padding: 3px 5px;
  background: var(--main-gradient);
  font-size: 13px;
}


a{
  color: var(--detail-color);
  text-decoration: none;
}
a:hover{
  color: var(--detail-color);
}
.searchDisplay h6{
  font-size: 14px;
  margin: 0;
}

.navBarFounded{
  color: white;
  font-size: 14px;
  margin: 0;
  padding: 0;
  width: 100%;
  text-align: start;
}
.navBarFounded:hover{
  background-color: var(--secondary-color);
}
.navSocailFunctions{
  width: auto;
  font-size: 15px;
  padding: 10px;
  background: var(--main-gradient);
  align-items: center;
  margin: 0;
  display: flex;
  justify-content: center;
}

.navSocailFunctions ul{
  display: flex;
  flex-direction: row;
  gap: 10px;
  padding: 0;
  align-items: center;
  justify-content: center;
  margin: 0;
  height: 100%;
}

.navSocailFunctions ul div{
  display: flex;
  gap: 3px;
  user-select: none;
  cursor: pointer;
}

.navSocailFunctions svg{
  font-size: 20px;
}
.navBarResponsiveBtn{
  display: none;
}
.navHomeBtn{
  display: none;
}
/*selectedPost*/
.selectedPost{
  background-color: white;
  width: 90%;
  margin-top: 25px;
  max-width: 1300px;
  border-radius: 15px;
  color: var(--detail-color);
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
  overflow: hidden;
  border: none;
  margin-bottom: 20px;
}

.selectedPost .btnOptionsContainer{
  width: 15vw;
  min-width: 100px;
  max-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  height: 100%;
}
.selectedPost svg{
  font-size: 20px;
}
.selectedPost .card-body{
  cursor: default;
}

.selectedPost .card-text{
  max-height: none;
}


/*Porfile Section*/
.logInForm{
  width: 50vw;
  background-color: white;
  padding: 20px;
  border-radius: 15px;
  color: var(--detail-color);
  margin: 5rem auto;
}

.logInForm button{
  background: var(--main-gradient);
  border: none;
}

/*Forums*/

.forumFormModal{
  position: fixed;
  top: 0;
  background-color: rgba(80, 80, 80, 0.9);
  padding: 20px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.forumForm{
  align-items: center;
  height: 350px;
  width: 70%;
  background-color: var(--detail-color);
  color: white;
  border-radius: 10px;
  padding: 30px;
}


.forumsContainer{
  padding: 10px;
  display: flex;
  height: auto;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-bottom: 1vh;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.forum{
  min-height: 150px;
  width: 350px;
  background-color: white;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 10px;
  border-radius: 10px;
}

.forum:hover{
  transform: scale(1.03);
}

.forumSearchBar input{
  width: 250px;
  height: 100px;
  border: none;
  border-bottom: 1px solid var(--detail-color);
  font-size: 60px;
  background-color: transparent;
  transition: border .2s, width .5s, font-size .31s;
  color: var(--detail-color);
}

.forumSearchBar input:focus-visible{
  font-size: 75px;
  outline: none;
  border-bottom:3.5px solid var(--detail-color);
  width: 550px;
}

.forumSearchBar svg{
  font-size: 60px;
  transition: .7s;
}

.forumSearchBarOn svg{
  font-size: 75px;
}

.forumSearchBar input::placeholder{
  font-size: 50px;
  color: var(--detail-color);
  transition: .2s;
}
.forumSearchBar input:focus-visible::placeholder{
  font-size: 55px;
  color: var(--detail-color);
}

.btn-forum{
  background-color: var(--detail-color);
  color: var(--detail-color);
  color: white;  
}

@media (max-width: 600px) {
  .navBar{
    position: absolute;
    flex-direction: column;
    height: auto;
    transition: .6s;
    z-index: 10;
    top: -100%;
    overflow: hidden;
    border-radius: 0px;
  }
  .navBarOpen{
    transition: .6s;
    border-radius: 0px 0px 15px 15px;
    top:7vh;    
  }
  
  .navSearch{
    border-radius: 10px;
    width: 100%;
  }

  .navBarResponsiveBtn .navText{
    font-size: 20px;
    background:none ;
    background-color: transparent;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
  }
  .navText{
    display: none;

  }

  .navSocailFunctions{
    border-radius: 10px;
  }

  .navSocailFunctions ul{
    font-size: 20px;
    width: 100%;
    justify-content: space-evenly;
  }

  .navSocailFunctions svg{
    font-size: 30px;
  }

  .navSearch input{
    width: 95%;
  }
  .navBarResponsiveBtn button{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    margin: 0;
    padding: 0;
  }

  .navHomeBtn{
    display: flex;
    font-size: 25px;
    justify-content: center;
    border-radius: 10px;
  }

  .navBarResponsiveBtn{
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    background: var(--main-gradient);
    width: 100vw;
    position: fixed;
    height: 7vh;
    font-size: 40px;
    color: var(--detail-color);
  }

  hr.my-5{
    margin: 5vh !important;
  }

  .logInForm{
    width: 95%;
    margin: 10px;
  }
  .mainSection form{
    width: 95vw !important;
  }
  .post{
    min-width: none;
    width: 95vw;
  }
  .forumForm{
    width: 100%;
  }
  .btn-forum{
    opacity: 0.5;
  }
  .forumSearchBar input:focus-visible {
    width: 250px;
  }

  .forumForm small{
    display: none;
  }

}